@keyframes pulse {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0.5);
    opacity: 0;
  }
}

@keyframes bg-animate {
  0% {
    transform: translateY(0);
    opacity: 1;
    border-radius: 0;
  }

  100% {
    transform: translateY(-1000px);
    opacity: 0;
    border-radius: 50%;
  }
}
