@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;700&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

body {
  font-family: 'Montserrat', sans-serif;
  padding: 0px;
  margin: 0px;
  background-color: #f8b943;
  overflow: hidden;
  user-select: none;
}

button,
button:focus,
button:active {
  outline: none;
  font-family: 'Montserrat', sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.container {
  height: 100svh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.bg {
  position: absolute;
  width: 100vw;
  height: 100vh;
}

.bg li {
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  animation: bg-animate 25s linear infinite;
  bottom: -150px;
}

.bg li:nth-child(1) {
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}

.bg li:nth-child(2) {
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.bg li:nth-child(3) {
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.bg li:nth-child(4) {
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.bg li:nth-child(5) {
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.bg li:nth-child(6) {
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.bg li:nth-child(7) {
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.bg li:nth-child(8) {
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.bg li:nth-child(9) {
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.bg li:nth-child(10) {
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  flex: 1;
}

.title {
  color: #fff;
  font-size: 20pt;
  font-weight: bold;
  margin: 0px 30px;
  width: 90vw;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.circle-button {
  background: none;
  border: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.circle-button > p {
  width: 350px;
  height: 350px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  color: #fff;
  font-size: 24pt;
  transition: all 0.3s;
}
.circle-button:hover > p {
  transform: scale(1.3);
}
.circle-button:active > p {
  transform: scale(1);
}
.circle-animated {
  animation-name: pulse;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.circle,
.circle-mid {
  width: 400px;
  height: 400px;
  background-color: #00000010;
  border-radius: 100%;
  transform: scale(1);
  transition: all 0.3s;
  cursor: pointer;
  opacity: 1;
}
.circle-button:hover > .circle,
.circle-button:hover > .circle-mid {
  background-color: #00000015;
}
.circle {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.circle-mid {
  width: 350px;
  height: 350px;
}

.button {
  position: relative;
  z-index: 999;
  padding: 20px;
  width: 100px;
  background: #fff;
  border: none;
  border-radius: 40px;
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 18pt;
  color: #f00;
}

.emoji {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 999;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-size: 45pt;
  text-align: center;
}
